// Dinamik API URL detection sistemi
// Backend i√ßin 8xxx portlarƒ± (8001, 8000, 8002, vb.) kontrol eder
// Frontend i√ßin 5xxx portlarƒ± ile √ßalƒ±≈üabilir

interface PortConfig {
  backendPorts: number[];
  frontendPorts: number[];
}

const portConfig: PortConfig = {
  backendPorts: [8001, 8000, 8002, 8003, 8004], // 8001'i ilk sƒ±raya aldƒ±m - stable port
  frontendPorts: [3001, 3000, 5000, 5001, 5173], // Frontend i√ßin √∂ncelik sƒ±rasƒ±
};

// API URL cache - Global deƒüi≈ükenler
let cachedApiUrl: string | null = null;
let apiUrlPromise: Promise<string> | null = null;

// Sync API Base URL - fallback for components that need immediate access
export const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:8001';

// Cache temizleme fonksiyonu
export const clearApiUrlCache = () => {
  console.log('üßπ Clearing API URL cache');
  cachedApiUrl = null;
  apiUrlPromise = null;
};

// Backend port detection
const detectBackendPort = async (): Promise<string> => {
  console.log('üîç Starting backend port detection...');
  
  // Environment variable varsa onu kullan
  if (process.env.REACT_APP_API_URL) {
    console.log('‚úÖ Using environment variable:', process.env.REACT_APP_API_URL);
    return process.env.REACT_APP_API_URL;
  }

  // Test environment check - force port 8000 for tests
  if (process.env.NODE_ENV === 'test') {
    console.log('üß™ Test mode - forcing port 8000');
    return 'http://localhost:8000';
  }

  // Backend portlarƒ±nƒ± sƒ±rayla test et
  for (const port of portConfig.backendPorts) {
    try {
      console.log(`üîç Testing backend on port ${port}...`);
      const response = await fetch(`http://localhost:${port}/health`, {
        method: 'GET',
        signal: AbortSignal.timeout(2000), // 2 saniye timeout
      });
      
      if (response.ok) {
        console.log(`‚úÖ Backend detected on port ${port}`);
        return `http://localhost:${port}`;
      }
    } catch (error: any) {
      // Port ula≈üƒ±labilir deƒüil, bir sonrakini dene
      console.log(`‚ùå Backend not found on port ${port}:`, error.message);
    }
  }

  // Hi√ßbir port √ßalƒ±≈ümƒ±yorsa varsayƒ±lan port
  console.warn('‚ö†Ô∏è No backend found, using default port 8001');
  return 'http://localhost:8001';
};

export const getApiUrl = async (): Promise<string> => {
  console.log('üì° getApiUrl called, cachedApiUrl:', cachedApiUrl);
  
  // Test ortamƒ±nda her zaman cache'i temizle
  if (process.env.NODE_ENV === 'test') {
    console.log('üß™ Test mode - clearing cache for fresh detection');
    clearApiUrlCache();
  }
  
  // Cache varsa onu kullan
  if (cachedApiUrl) {
    console.log('üìã Using cached API URL:', cachedApiUrl);
    return cachedApiUrl;
  }

  // Zaten bir detection √ßalƒ±≈üƒ±yorsa aynƒ± promise'i bekle
  if (apiUrlPromise) {
    console.log('‚è≥ Detection already in progress, waiting...');
    return apiUrlPromise;
  }

  // Yeni detection ba≈ülat
  console.log('üöÄ Starting new detection...');
  apiUrlPromise = detectBackendPort();
  
  try {
    cachedApiUrl = await apiUrlPromise;
    console.log('‚úÖ Detection complete, cached URL:', cachedApiUrl);
    return cachedApiUrl;
  } catch (error) {
    console.error('‚ùå Backend detection failed:', error);
    cachedApiUrl = 'http://localhost:8001'; // Fallback
    console.log('üîÑ Using fallback URL:', cachedApiUrl);
    return cachedApiUrl;
  } finally {
    apiUrlPromise = null; // Promise'i temizle
  }
};

// Development/Production mode detection
export const isDevelopment = process.env.NODE_ENV === 'development';
export const isProduction = process.env.NODE_ENV === 'production';
export const isTest = process.env.NODE_ENV === 'test';

// Current frontend port detection
export const getCurrentPort = (): number => {
  if (typeof window === 'undefined') return 3000; // SSR or test environment
  const port = parseInt(window.location.port);
  return port || (window.location.protocol === 'https:' ? 443 : 80);
};

// Backend health check
export const checkBackendHealth = async (apiUrl?: string): Promise<boolean> => {
  try {
    const url = apiUrl || await getApiUrl();
    const healthUrl = url.replace('/api', '/health');
    
    const response = await fetch(healthUrl, {
      method: 'GET',
      signal: AbortSignal.timeout(5000),
    });
    
    return response.ok;
  } catch (error) {
    console.error('Backend health check failed:', error);
    return false;
  }
};

// Force cache temizleme - sadece gerekli durumlarda
if (typeof window !== 'undefined' && process.env.NODE_ENV !== 'test') {
  console.log('üîÑ Initial page load - clearing API URL cache');
  clearApiUrlCache();
}

// Test ortamƒ±nda export edilen konfig√ºrasyon objesi
const apiConfig = {
  getApiUrl,
  clearApiUrlCache,
  checkBackendHealth,
  getCurrentPort,
  isDevelopment,
  isProduction,
  isTest,
};

export default apiConfig; 